import { ProductsService } from './../../services/products.service';
import { HttpClient } from '@angular/common/http';
import { NgForm } from '@angular/forms';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-add-products',
  templateUrl: './add-products.component.html',
  styleUrls: ['./add-products.component.css']
})
export class AddProductsComponent implements OnInit {

  constructor(public pdtsSer : ProductsService) { }

  selectedFile : string;

  categories : string[] = [];

  msg : any;

  ngOnInit() {

    this.pdtsSer.getCategories().subscribe((data:any[])=>{

      console.log(data);
      this.categories = data;

    }, (error)=>{

      console.log(error);

    });

  }

  onImgSelected(event:any)
  {
    console.log(event.target.files[0]);
    this.selectedFile = event.target.files[0];
  }

  createProducts(form:NgForm)
  {
    console.log(form.value);

   const fd = new FormData();

    fd.append("pdtCat", form.value.pdtCat);
    fd.append("pdtName", form.value.pdtName);
    fd.append("pdtPrice", form.value.pdtPrice);
    fd.append("pdtDesc", form.value.pdtDesc);
    fd.append("image", this.selectedFile, "pdtimage");

    console.log(fd);
    this.pdtsSer.addProducts(fd).subscribe((data)=>{

    this.msg =  data;
    }, (error)=> {
      console.log(error);
    });

    form.reset();
  }

}
