import { ProductsService } from './../../services/products.service';
import { Component, OnInit } from '@angular/core';
import { HttpErrorResponse } from '@angular/common/http';
import { Router } from '@angular/router';

@Component({
  selector: 'app-cart',
  templateUrl: './cart.component.html',
  styleUrls: ['./cart.component.css']
})
export class CartComponent implements OnInit {

  myCartItems : any[] = [];

  constructor(private pdtService : ProductsService, private router : Router) { }

  ngOnInit() {

    this.pdtService.getMyCartItems().subscribe((data:any[])=>{

      console.log("my cart items", data);
      this.myCartItems = data;
     // console.log("my data", this.myCartItems);
    }, (error)=>{

      
        console.log("my error", error);

        this.router.navigate(['/login']);

     
    })
  }

}
