import { AuthGuard } from './auth.guard';
import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { FormsModule } from '@angular/forms';

import { AppComponent } from './app.component';
import { HeaderComponent } from './Header/header/header.component';
import { CategoriesComponent } from './categories/categories.component';
import { ProductListComponent } from './Products/product-list/product-list.component';
import { NewsletterComponent } from './Footer/newsletter/newsletter.component';
import { FooterComponent } from './Footer/footer/footer.component';
import { LoginComponent } from './login/login.component';
import { NotfoundComponent } from './notfound/notfound.component';
import { CartComponent } from './Products/cart/cart.component';
import { TokenInterpectorService } from './services/token-interpector.service';
import { UploadComponent } from './upload/upload.component';
import { AddProductsComponent } from './Products/add-products/add-products.component';

const routes : Routes = [
  {path:'', component:ProductListComponent},
  {path:'category/:id', component:ProductListComponent},
  {path:'login', component:LoginComponent},
  {path:'mycart', component:CartComponent, canActivate:[AuthGuard]},
  {path:'addproducts', component:AddProductsComponent, canActivate:[AuthGuard]},
  {path:'**', component:NotfoundComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    CategoriesComponent,
    ProductListComponent,
    NewsletterComponent,
    FooterComponent,
    LoginComponent,
    NotfoundComponent,
    CartComponent,
    UploadComponent,
    AddProductsComponent
  ],
  imports: [
    BrowserModule,
    RouterModule.forRoot(routes),
    HttpClientModule,
    FormsModule
  ],
  providers: [
    AuthGuard,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterpectorService,
      multi:true
    }
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
