import { ProductsService } from './../../services/products.service';
import { Component, OnInit } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Router, ActivatedRoute, Params } from '@angular/router';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.css']
})
export class ProductListComponent implements OnInit {

  myProducts : any;

  catId :  string;

  constructor(private pdtService : ProductsService, public route : ActivatedRoute) { }

  ngOnInit() {

   // console.log(this.route.params._value.id);

    this.route.params.subscribe((params:Params)=>{

        console.log(params['id']);

        this.catId = params['id'];

       
        if(this.catId)
        {
          console.log("function invoked");
          this.pdtService.getproductsByCategory(this.catId).subscribe((data) => {
  
            console.log(data);
       
               this.myProducts = data;
             }, (error) => {
       
               console.log("my error", error);
             }, ()=> {
       
               console.log("Next from products");
             });
        }
       
    });

    if(!this.catId)
    {
      console.log("function invoked 2");
      this.pdtService.getproducts().subscribe((data) => {

        console.log(data);
   
           this.myProducts = data;
         }, (error) => {
   
           console.log("my error", error);
         }, ()=> {
   
           console.log("Next from products");
         });
   
    }

   
     // console.log("fsdfsd");

  }

  AddToMyCart(pdtId)
  {
    //alert(pdtId);
   this.pdtService.addToCart(pdtId).subscribe((data)=>{

    console.log("data", data);
    this.pdtService.cartUpdated.emit(data);
   }, (error)=>{

    console.log("error", error);
   });

    
  }

}
