import { UsersService } from './../services/users.service';
import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { Router } from '@angular/router';

declare var $: any;

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  msg : string;

  constructor(public userSer : UsersService, public router : Router) {

   
    console.log( this.userSer.isLoggedIn());
   }

  ngOnInit() {

    $('.toggle').click(function(){
      // Switches the Icon
    //  alert("cc");
      $(this).children('i').toggleClass('fa-pencil');
      // Switches the forms  
      $('.form').animate({
      height: "toggle",
      'padding-top': 'toggle',
      'padding-bottom': 'toggle',
      opacity: "toggle"
      }, "slow");
    });

  }

  doRegister(form:NgForm)
  {
    console.log(form);

    this.userSer.createUser(form.value).subscribe((data) => {
      console.log(data);
    })
  }

  doLogin(form:NgForm)
  {
  //  console.log(form.value);
    this.userSer.loginUser(form.value).subscribe((data:any) => {
      console.log(data);
      if(data.length!=0)
      {
        localStorage.setItem('token', data);
        this.msg = "Login Successfully";
        this.router.navigate(['/']);
      }
      else {
        this.msg = "Login Failed"
      }
    }, (error)=>{
      console.log(error);
      this.msg = "Something went wrong";
    })
  }

}
