import { HttpClient } from '@angular/common/http';
import { Injectable, EventEmitter } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class ProductsService {

  cartItems : number = 0;

  cartUpdated = new EventEmitter();

  constructor(public http : HttpClient) { }

  getproducts()
  {
    return this.http.get('http://localhost:3000/products');
  }

  getproductsByCategory(pdtCat:string)
  {
    return this.http.get('http://localhost:3000/products/'+pdtCat);
  }

  addToCart(pdtid:string)
  {
   // console.log('invoked', this.cartItems);
    //this.cartItems++;

    return this.http.post("http://localhost:3000/addcart/", {"productId":pdtid});

   // return this.getCartItems();
  }

  getCartItems()
  {
    return this.cartItems;
  }

  getMyCartItems()
  {
    return this.http.get('http://localhost:3000/mycart');
  }

  addProducts(data)
  {
    return this.http.post('http://localhost:3000/addproducts', data);
  }

  getCategories()
  {
    return this.http.get("http://localhost:3000/getcategories");
  }


}
